/* Global */
body {
  font-family: Cambria, Georgia, Times, 'Times New Roman', serif;
}

div.link {
  cursor: pointer; 
}

h2 {
  color: rgb(76, 0, 139);
}

h3 {
  color: rgb(109, 9, 172);
}

/* Content */
div.content-container {
  width: 90%;
  margin: auto;
  overflow: auto;
}

div.content {
  float: left;
  width: 99%;
}

div.content img {
  width: 630px;
  height: auto;
}


div.external-link a {
  text-decoration: none;
  color: rgb(0, 121, 61);
  cursor: pointer; 
}

div.external-link {
  padding: 8px;
  text-align: right;
}

div.external-link a:hover {
  text-decoration: underline;
}


/* Menu */
div.menu a {
  text-decoration: none;
  color: rgb(0, 62, 121);
  cursor: pointer; 
}

div.menu a:hover {
  text-decoration: underline;
}

div.menu {
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  background-color: rgb(185, 220, 240);
  width: 95%;
  margin: auto;
}

div.menu h1 {
  color: rgb(1, 51, 80);
}

/* Footer */
div.footer {
  background-color: rgb(185, 220, 240);
  padding: 3px;
  top: 10px;
  padding-left: 15px;
  margin: auto;
  margin-top: 10px;
}

/* Togglable */
div.togglable {
  width: 150px;
  padding: 5px;
  margin: 8px 0px 5px 0px;
  background-color:  rgb(211, 241, 230);
  border-color: rgb(91, 180, 103);
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  cursor: pointer; 
}

div.togglable:hover {
  text-decoration: underline;
}

/* Buttons */
div.small-button {
  display:inline-block;
  margin-left: 5px;
  width: 50px;
  padding: 3px;
  background-color:  rgb(211, 241, 230);
  border-color: rgb(91, 180, 103);
  border-style: solid;
  border-width: 1px;
  border-radius: 4px;
  text-align: center;
  cursor: pointer; 
}

/* Search */
img.search-img {
  max-height: 500px;
  width: auto;
}

/* APOD */
div.apodImg {
  text-align: center;
  min-height: 650px;
}

div.previous-apod {
  padding: 10px 0px 10px 50px;
}


/* Search results */
div.result-dark {
  padding: 5px 10px 10px 10px;  
  font-family: Cambria, Georgia, Times, 'Times New Roman', serif;
  background-color: rgb(228,239,245);
}

div.result-bright {
  padding: 5px 10px 10px 10px;  
  font-family: Cambria, Georgia, Times, 'Times New Roman', serif;
  background-color: white;
}

/* Notification */
div.notif {
  background-color: pink;
  line-height: 30px;
  border-width: 2px;
  border-color: red;
  border-style: solid;
  border-radius: 3px;
  text-align: center;
  vertical-align: middle;
  padding: 10px;
}

div.empty-notif {
  background-color: white;
  height: 30px;
}

div.notif-div {
  float: left;
  padding: 0px 0px 0px 250px;
  margin-top: -90px;
}

div.exclamation {
  font-size: xx-large;
  float: left;
  padding: 3px;
}